@import url(https://fonts.googleapis.com/css2?family=Architects+Daughter&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);

.inforBar {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: rgb(87,217,178);
    background: linear-gradient(180deg, rgba(87,217,178,1) 0%, rgba(34,193,195,0.6993172268907564) 14%, rgba(32,165,180,1) 100%, rgba(24,55,122,1) 100%);
    border-radius: 4px 4px 0 0;
    height: 60px;
    width: 100%;
}

  .inforBar-inner{
    flex: 0.5 1;
    display: flex;
    align-items: center;
    margin-left: 5%;
    color: white;
}

.InforBar-rightContainer {
    display: flex;
    flex: 0.5 1;
    justify-content: flex-end;
     margin-right: 5%;
}

    .InforBar-rightContainer > a,   .InforBar-rightContainer > a:visited{
        color:white;
    }
    .onlineIcon {
    margin-right: 5%;
    }

    .blink{
        -webkit-animation: blinker 1.5s linear infinite;
                animation: blinker 1.5s linear infinite;
    }

    @-webkit-keyframes blinker {
        50% {
          opacity: 0;
        }
    }

    @keyframes blinker {
        50% {
          opacity: 0;
        }
    }
.form {
    display: flex;
    border-top: 2px solid #D3D3D3;
    height: 60px;
  }

  .message_input {
    border: none;
    border-radius: 0;
    width: 100%;
    font-size: 1.2em;
    border-radius: 0 0px 5px 5px;
  }

  input.message_input::-webkit-input-placeholder{
    color:#D0D0D0;
  }

  input.message_input:-ms-input-placeholder{
    color:#D0D0D0;
  }

  input.message_input::-ms-input-placeholder{
    color:#D0D0D0;
  }

  input.message_input::placeholder{
    color:#D0D0D0;
  }

  input:focus, textarea:focus, select:focus{
    outline: none;
  }

  .message_input::-webkit-input-placeholder{
    font-style: italic;

  }

  .message_input:-ms-input-placeholder{
    font-style: italic;

  }

  .message_input::-ms-input-placeholder{
    font-style: italic;

  }

  .message_input::placeholder{
    font-style: italic;

  }
  .sendButton {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #0ba0c0;
    padding: 20px;
    display: inline-block;
    border: none;
    width: 20%;
    cursor: pointer;
    border-radius: 0 0px 5px 5px;
  }
.message__Box {
    background: #F3F3F3;
    border-radius: 20px;
    padding: 5px 20px;
    color: white;
    display: inline-block;
    max-width: 100%;
  }

  .message__Text {
    width: 100%;
    letter-spacing: 0;
    float: left;
    font-size: 1.1em;
    word-wrap: break-word;
  }

  .message__Text img {
    vertical-align: middle;
  }

  .message__Container {
    display: flex;
    justify-content: flex-end;
    padding: 0 2%;
    margin-top: 3px;
  }

  .sent__Text {
    display: flex;
    align-items: center;
    font-family: Helvetica;
    color: #828282;
    letter-spacing: 0.3px;
  }
  .sent__Time{
    display: flex;
    justify-content: center;
    color: #828282;
    font-size: 10px;
  }

  .pl-10 {
    padding-left: 10px;
  }

  .pr-10 {
    padding-right: 10px;
  }

  .justify__start {
    justify-content: flex-start;
  }

  .justify__end {
    justify-content: flex-end;
  }

  .color__White {
    color: white;
  }

  .color__Dark {
    color: #353535;
  }

  .background__Blue {

    background: rgb(34,193,195);
    background: linear-gradient(180deg, rgba(34,193,195,1) 0%, rgba(24,55,122,1)  220%);}

  .background__Light {
    background: #F3F3F3;
  }

  .fadeIn {
    -webkit-animation-name: fadeIn;
    animation-name: fadeIn;
 }

  @-webkit-keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }

  @keyframes fadeIn {
    0% {opacity: 0;}
    100% {opacity: 1;}
 }
.messages {
    padding: 5% 0;
    overflow: auto;
    flex: auto;
  }
.textContainer {
    display: flex;
    flex-direction: column;
    color: white;
    height: 100%;
    flex: 0.35 1;
    justify-content: space-between;
    background: #0ba0c0;
    border-right: 5px solid #ACD957;

  }

  .activeContainer {
    display: flex;
    align-items: center;
    margin-bottom: 50%;
    padding-left: 16%;
  }

  .meeting__room{
    margin-top: 10%;
    background: #088099;
    display: flex;
    padding: 10px;
    padding-right: 0;
    border-radius: 10px 0px 0px 10px;
    font-size: 1.3rem;
    letter-spacing: 2px;
    font-weight: 200;
    border-bottom: 2px solid rgb(34,193,195);
  }


  .activeContainer > h2{
    width: 100%;
    font-family: 'Montserrat', sans-serif;
  }

  .activeContainer > h2 > .activeItem{
    background: #0990AC;
    width: 100%;
    padding: 10px;
    margin-bottom: 5%;
    border-radius: 10px 0px 0px 10px;
    font-size: 1.3rem;
    border-bottom: 2px solid rgb(34,193,195);
  }

  .activeItem {
    display: flex;
    align-items: center;
  }

  .activeContainer img {
    padding-left: 10px;
  }

  .textContainer h1 {
    margin-bottom: 0px;
    padding-left: 5%;
  }

  @media (min-width: 320px) and (max-width: 1200px) {
    .textContainer {
      display: none;
    }
  }
.outerContainer {
    flex: 0.5 1;
    display: flex;
    align-items: center;
    height: 100vh;
    background-color: #f6f6f6;
  }

  .innerContainer{
    display: flex;
    width: 100%;
    height: 100%;

  }

  .message__container{
    width: 100%;
    flex: 1 1;
    height: 100%;
    justify-content: center;
    align-items: center;
    align-content: center;
    display: flex;
    background-size: cover;
    background-repeat: no-repeat;
  }

  .container {
    box-shadow: 0px 7px 15px rgba(0,0,0,0.2);
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    background: #FFFFFF;
    border-radius: 8px;
    height: 600px;
    width: 80%;
  }

  .error{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .error > div> img {
    height: 600px;
  }

  .error > a > button {
    color: #fff !important;
    text-transform: uppercase;
    text-decoration: none;
    background: #0ba0c0;
    padding: 15px;
    border-radius: 5px;
    display: inline-block;
    border: none;
    font-size: 15px;
    letter-spacing: 2px;
    cursor: pointer;
    box-shadow:inset -5px -11px 16px -2px #0ba0c0;
    font-family: 'Montserrat', sans-serif;
}

.error >  a > button:hover{
  background: #0990AC;
  box-shadow:inset -5px -11px 16px -2px #088099;

}

  @media (min-width: 320px) and (max-width: 480px) {
    .outerContainer {
      height: 100%;
    }

    .container {
      width: 100%;
      height: 100%;
    }
  }

  @media (min-width: 480px) and (max-width: 1200px) {
    .container {
      width: 60%;
    }
  }
